import React from 'react'
import '../App.css'

const RandomScreen = (props) => (
  props.random.title ? (
    <div className="Random-screen">
      <div className="Random-tune" onClick={props.getNewRandomTune}>
        <span className="Tune-title">{props.random.title}</span>
        <span className="Tune-detail">{props.random.composer} ({props.random.year})</span>
      </div>
    </div>
  ) : (
    <div className="Random-screen">
      <div className="Random-tune">
        <span className="Tune-detail">Loading random tune...</span>
      </div>
    </div>
  )
)

export default RandomScreen


