import React from 'react'
import Hammer from 'hammerjs'
import { TweenLite, TimelineLite } from 'gsap'
import '../App.css'

class SetlistTune extends React.Component {
  componentDidMount() {
    const node = this.refs.setlistTune
    this.hammer = Hammer(node)
    this.hammer.on('panmove', (e) => this.handlePanMove(e))
    this.hammer.on('panend', (e) => this.handlePanEnd(e))
  }

  componentWillUnmount() {
    this.hammer.off('panmove', (e) => this.handlePanMove(e))
    this.hammer.off('panend', (e) => this.handlePanEnd(e))
  }

  handlePanMove(e) {
    const node = this.refs.setlistTune
    if (e.deltaX <= 0) {
      node.style.transform = `translateX(${e.deltaX}px)`
    }
  }

  handlePanEnd(e) {
    const node = this.refs.setlistTune
    if (e.deltaX > -125) {
      TweenLite.to(node, .15, {x: 0})
    }
    else {
      const tl = new TimelineLite({
        onComplete: () => this.props.toggleSelected(this.props.tune)
      }).to(node, .2, {x: -window.innerWidth})
        .to(node, .1, {height: 0})
    }
  }

  render() {
    const { tune } = this.props

    return (
      <li className="SetlistTuneContainer">
        <div className="SetlistTune" ref="setlistTune">
          <span className="Tune-title">{tune.title}</span>
          <span className="Tune-detail">{tune.composer} ({tune.year})</span>
        </div>
      </li>
    )
  }
}

export default SetlistTune


