import React from 'react'
import '../App.css'

class Tune extends React.Component {
  constructor(props) {
    super(props)
    this.handleClick = this.handleClick.bind(this)
  }

  render() {
    const { tune } = this.props

    return (
      <li className="Tune" style={{backgroundColor: tune.selected ? '#e1eff8' : 'white'}}
        onClick={this.handleClick}
      >
        <span className="Tune-title">{tune.title}</span>
        <span className="Tune-detail">{tune.composer} ({tune.year})</span>
      </li>
    )
  }

  handleClick() {
    this.props.toggleSelected(this.props.tune)
  }
}

export default Tune


